@import url("https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700");
a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:after,
blockquote:before,
q:after,
q:before {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
* {
  box-sizing: border-box;
}
body {
  font-family: "Roboto", sans-serif;
  background: #19202a;
}
header {
  position: absolute;
  z-index: 3;
  padding: 15px;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
header .logo {
  width: 50px;
}
header .logo img {
  width: 100%;
}
header nav {
  -ms-flex-item-align: center;
  -ms-grid-row-align: center;
  align-self: center;
}
header nav a {
  color: white;
  text-decoration: none;
  font-weight: 300;
  font-size: 1.2rem;
  padding: 0 10px;
}
#top-section {
  position: relative;
  height: 100vh;
}
#top-section .blurredbg {
  background-image: linear-gradient(
      45deg,
      #19202a 0%,
      #19202a 33%,
      rgba(25, 32, 42, 0.96) 36%,
      rgba(25, 32, 42, 0.86) 43%,
      rgba(176, 38, 183, 0.57) 62%,
      rgba(125, 185, 235, 0) 100%
    ),
    url("./assets/images/tekashi.png");
  background-size: cover;
  height: 100vh;
  width: 100%;
  filter: blur(50px) saturate(130%);
  position: absolute;
  z-index: 1;
}
#top-section .floating-img {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: end;
  align-items: flex-end;
}
#top-section .tekashi-img {
  width: 30%;
}
#top-section .tekashi-img img {
  width: 100%;
  filter: contrast(150%);
}
.container {
  max-width: 1400px;
  margin: 0 auto;
}
#bio {
  background: black;
  position: relative;
  z-index: 3;
  padding: 100px 0;
}
#bio .container {
  display: -ms-flexbox;
  display: flex;
}
#bio .container p {
  padding: 0 50px;
  width: 50%;
  color: white;
  line-height: 1.8;
  font-size: 0.9rem;
}
#bio .container .logo-centered {
  display: -ms-flexbox;
  display: flex;
  width: 50%;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 0 50px;
}
#media {
  background-image: url("/assets/images/bandana.jpg");
  background-size: cover;
  padding: 100px 0;
}
#media .title {
  background: black;
  font-size: 1.1rem;
  font-weight: 300;
  text-transform: uppercase;
  padding: 20px;
  text-align: center;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  color: white;
}
#media .videos {
  padding: 80px 50px 0;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 50% 50%;
  grid-template-columns: 50% 50%;
  grid-gap: 50px;
}
#media .videos .video {
  border-radius: 4px;
  overflow: hidden;
  height: 300px;
  box-shadow: 0 13px 24px 8px rgba(0, 0, 0, 0.75);
}
#media .videos .video iframe {
  height: 100%;
}
#music {
  padding: 80px 0;
  position: relative;
}
#music .blurredbg {
  background-image: linear-gradient(
      45deg,
      #fffd3c 0%,
      #fffd3c 33%,
      rgba(255, 253, 60, 0.96) 36%,
      rgba(255, 253, 60, 0.86) 43%,
      rgba(255, 253, 60, 0.2) 62%,
      rgba(125, 185, 232, 0) 100%
    ),
    url("/assets/images/album69.jpg");
  background-size: cover;
  height: 100%;
  width: 100%;
  filter: blur(50px) saturate(130%);
  position: absolute;
  z-index: 1;
}
#music .container {
  display: -ms-flexbox;
  display: flex;
  padding: 0 50px;
  position: relative;
  z-index: 3;
}
#music .info {
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
}
#music .info .title {
  font-size: 3rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 15px;
}
#music .info .sub-title {
  font-size: 1.7rem;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 100px;
}
#music .info .singles {
  width: 400px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 50% 50%;
  grid-template-columns: 50% 50%;
  margin-bottom: 80px;
}
#music .info li {
  font-size: 1.5rem;
  font-weight: 300;
  text-align: center;
  margin-bottom: 20px;
}
#music .info .logos {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 4;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}
#music .album-cover {
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
}
#music .album-cover img {
  width: 50%;
}
#tour {
  background: black;
  position: relative;
  z-index: 4;
}
#tour .top-img {
  background: url("./assets/images/group.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  filter: saturate(0%) contrast(150%);
  height: 300px;
}
#tour .tourdates {
  background: black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
  align-items: center;
  padding: 80px 0;
}
#tour .tourdates .title {
  color: white;
  text-transform: uppercase;
  font-size: 1.8rem;
  text-align: center;
  margin: 0 0 80px;
}
#tour .tourdates .dates {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 3;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
}
#tour .tourdates .dates li {
  color: white;
  font-size: 1.3rem;
}
#tour .tourdates .dates li span {
  padding: 0 10px;
}
#tour .tourdates .dates li .city {
  font-weight: 700;
}
#tour .tourdates .dates li .location {
  font-size: 1rem;
  font-weight: 300;
}
#tour .tourdates .dates li .date {
  font-weight: 500;
  color: #f14848;
}
footer {
  background: #1b1b1b;
  position: relative;
  z-index: 4;
  padding: 25px;
  text-align: center;
  color: white;
}
footer img {
  max-width: 45px;
}
footer a {
  color: red;
  text-decoration: none;
}
footer div {
  padding-top: 25px;
}
